.gallery-list {
    display: flex;
    flex-wrap: wrap;
}

.card {
    
    // @media screen and (max-width: 767px)
    @include less-tab {
        margin: 0 auto;
        width: 280px;
        
        &:not(:last-child) {
            margin-bottom: 20px;
        }
    }

    @include tab {
        margin-left: 32px;
        margin-top: 32px;
        flex-basis: calc(100% / 2 - 32px);
    }

    @include desk {
        flex-basis: calc(100% / 3 - 32px);
    }

    cursor: pointer;

    &-img {
        margin-bottom: 10px;
        width: 100%;
        border-radius: 5px;

    }

    &-title {
        margin-bottom: 2px;
        font-size: 12px;
        line-height: 1.16;
        color: $blackColor;
    }

    &-description {
        line-height: 1.33;
        color: $accentColor;
        
    }

    &-rating {
        padding: 1px 6px;
       
        margin-left: 9px;
        line-height: 1.16;
        background-color: $accentColor;
        color: $whiteColor;
        border-radius: 5px;
    }
}
